              fill="#67CC79" p-id="5044"></path>
          </svg>
        </div>
      </div>
    </div>
    <div class="row" v-if="groups.length > 0">
      <b class="col-12 p-2 mb-1">公开群</b>
      <div class="col-12">
        <a :href="link.url" target="_blank" class="d-inline-block m-1" v-for="link in groups" v-html="link.icon">

        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ContactInfo",
  data() {
    return {
      blogs: [],
      wemedia: [],
      partnr: [],
      groups: []
    }
  },
  mounted() {
    this.getContact();
  },
  methods: {
    getContact() {
      const blog = Bmob.Query('LinkTable');
      blog.equalTo("type", "==", "博客");
      blog.find().then(res => {
        this.blogs = res
      }).catch(function (e) {
      });
      const wm = Bmob.Query('LinkTable');
      wm.equalTo("type", "==", "自媒体");
      wm.find().then(res => {
        this.wemedia = res
      }).catch(function (e) {
      });
      const par = Bmob.Query('LinkTable');
      par.equalTo("type", "==", "联系方式");
      par.find().then(res => {
        this.partnr = res
      }).catch(function (e) {
      });
      const group = Bmob.Query('LinkTable');
      group.equalTo("type", "==", "公开群");
      group.find().then(res => {
        this.groups = res
      }).catch(function (e) {
      });
    }
  }
}
</script>

<style scoped>

</style>



