<template>
  <div>
    <div v-for="p in passages">
      <passage-item :item="p"></passage-item>
    </div>
  </div>
</template>

<script>
import PassageItem from "./PassageItem";
export default {
  name: "PassageList",
    components: {PassageItem},
    data(){
      return{
        passages:[]
      }
    },
    mounted() {
      this.getPassageList()
    },
    methods:{
      getPassageList() {
        console.log("---")
        const query = Bmob.Query("PassageList");
        query.limit(10);
        query.order("-createdAt");
        query.find().then(res => {
          this.passages = res
          console.log(this.passages)
        });
      }
    }
}
</script>

<style scoped>

</style>



